@font-face {
  font-family: 'font-bold';
  src: url("../fonts/32F6BB_0_0.eot");
  src: url("../fonts/32F6BB_0_0.eot?#iefix") format("embedded-opentype"), url("../fonts/32F6BB_0_0.woff2") format("woff2"), url("../fonts/32F6BB_0_0.woff") format("woff"), url("../fonts/32F6BB_0_0.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'font-regular';
  src: url("../fonts/32F6BB_1_0.eot");
  src: url("../fonts/32F6BB_1_0.eot?#iefix") format("embedded-opentype"), url("../fonts/32F6BB_1_0.woff2") format("woff2"), url("../fonts/32F6BB_1_0.woff") format("woff"), url("../fonts/32F6BB_1_0.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'font-mono';
  src: url("../fonts/monosten-c.eot");
  src: url("../fonts/monosten-c.eot?#iefix") format("embedded-opentype"), url("../fonts/monosten-c.woff") format("woff"), url("../fonts/monosten-c.woff2") format("woff2"), url("../fonts/monosten-c.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

body {
  background: #eaeaea;
  font-family: 'font-regular';
  font-size: 12pt;
}

body.page-template-page-yellow, body.blog, body.single-post {
  background-color: #f7e463;
}

a {
  color: inherit;
}

a:hover {
  color: inherit;
}

small {
  font-family: 'font-mono';
}

strong {
  font-family: 'font-bold';
  font-weight: normal;
}

p {
  margin: 0 0 20px;
}

h1 {
  font-family: 'font-bold';
  font-weight: normal;
  font-size: 20pt;
  margin: 0 0 20px;
}

h2 {
  font-weight: normal;
  font-size: 20pt;
  margin: 0 0 20px;
}

h3 {
  font-family: 'font-bold';
  font-weight: normal;
  font-size: 16pt;
  margin: 0;
}

h4 {
  font-family: 'font-bold';
  font-weight: normal;
  font-size: 12pt;
  margin: 0;
  text-transform: uppercase;
}

hr {
  border: 0;
  border-top: 1px solid black;
}
